.block-portfolio__container {
  position: relative;
  margin: 0 auto;
  width: 320px;
  height: 298px;
  color: white;
  background-image: url("../img/brooke-lark-229136-unsplash-m.jpg");
}

.block-portfolio__background {
  position: absolute;
  top: 0;
  width: 320px;
  margin: 0 auto;
  min-height: 298px;
  opacity: 0.4;
  border-radius: 2px;
  background-color: #000000;
}

.block-portfolio__wrapper {
  display: flex;
  padding-top: 77px;
  margin-left: 46px;
}

.block-portfolio__color {
  z-index: 3;
}

.block-portfolio__header {
  margin: 0;
  margin-bottom: 21px;
  font-size: 18.5px;
  font-weight: 600;
}

.block-portfolio__description {
  margin: 0;
  margin-bottom: 33px;
  width: 197px;
  font-size: 14px;
}

.block-portfolio__button {
  position: relative;
  padding-bottom: 5px;
  text-transform: uppercase;
  color: white;
  @include font(14px, 15px, 600);
  letter-spacing: 1.4px;
  border-bottom: 2px solid white;
}

.block-portfolio__color img {
  position: absolute;
  top: 194px;
  left: 200px;
}

@media (min-width: $tablet-width) {
  .block-portfolio {
    width: 768px;
    margin: 0 auto;
  }

  .block-portfolio__container {
    margin: 0;
    width: 261px;
    height: 274px;
    background-image: url("../img/brooke-lark-229136-unsplash-tab.jpg");
    background-repeat: no-repeat;
    background-position: 100%;
  }

  .block-portfolio__background {
    top: -63px;
    width: 602px;
    opacity: 1;
    background-color: #f8f8f8;
    z-index: -1;
  }

  .block-portfolio__wrapper {
    padding-top: 39px;
    margin-left: 283px;
    color: #212021;
  }

  .block-portfolio__header {
    width: 265px;
    font-size: 24px;
    font-weight: 600;
  }

  .block-portfolio__description {
    width: 225px;
    font-size: 16px;
  }

  .block-portfolio__button {
    font-size: 15.5px;
    font-weight: bold;
    letter-spacing: 1.6px;
    color: #212021;
    border-bottom: 2px solid #212021;
  }

  .block-portfolio__color img {
    top: 150px;
    left: 471px;
    width: 32px;
    height: 36px;
  }
}

@media (min-width: $desktop-width) {
  .block-portfolio {
    width: 1440px;
    margin: 0 auto;
  }

  .block-portfolio__background {
    width: 970px;
    min-height: 375px;
  }

  .block-portfolio__container {
    width: 611px;
    height: 373px;
    background-image: url("../img/brooke-lark-229136-desk.jpg");
  }

  .block-portfolio__wrapper {
    padding-top: 65px;
    margin-left: 641px;
  }

  .block-portfolio__color img {
    top: 208px;
    left: 820px;
    width: 24px;
    height: 28px;
  }

  .block-portfolio__description {
    width: 260px;
    line-height: normal;
    font-size: 18px;
  }
}
