.hidden-menu {
  display: block;
  position: fixed;
  list-style: none;
  padding: 10px;
  padding-top: 120px;
  margin: 0 auto;
  box-sizing: border-box;
  width: 100%;
  background-color: #f8f8f8;
  height: 480px;
  top: 0;
  right: -100%;
  transition: right .2s;
  z-index: 2;
  transform: translateZ(0);
  backface-visibility: hidden; }

.hidden-menu-ticker {
  display: none; }

.btn-menu {
  padding: 5px;
  position: fixed;
  top: 5px;
  right: 90px;
  cursor: pointer;
  transition: right .23s;
  z-index: 6;
  width: 25px;
  transform: translateZ(0);
  backface-visibility: hidden; }

.btn-menu span {
  display: block;
  height: 2px;
  background-color: #ffffff;
  margin: 5px 0 0;
  transition: all .1s linear .23s;
  position: relative; }

.btn-menu span.first {
  margin-top: 0; }

.btn-menu span.second,
.btn-menu span.third {
  width: 15px;
  margin-left: 10px; }

.hidden-menu-ticker:checked ~ .btn-menu {
  right: 160px; }

.hidden-menu-ticker:checked ~ .hidden-menu {
  right: 0; }

.hidden-menu-ticker:checked ~ .btn-menu span.first {
  transform: rotate(45deg);
  top: 10px;
  right: -70px;
  background-color: black; }

.hidden-menu-ticker:checked ~ .btn-menu span.second {
  opacity: 0; }

.hidden-menu-ticker:checked ~ .btn-menu span.third {
  transform: rotate(-45deg);
  top: -4px;
  right: -70px;
  width: 25px;
  background-color: black;
  margin-left: 0; }

@media (min-width: 768px) {
  .hidden-menu-wrapper {
    display: block; }
  .hidden-menu {
    padding-top: 234px;
    height: 825px;
    z-index: 5; }
  .btn-menu {
    top: 25px;
    right: 430px; }
  .btn-menu span {
    background-color: black; }
  .btn-menu span.second,
  .btn-menu span.third {
    margin-left: 0; } }

@media (min-width: 1300px) {
  .hidden-menu-wrapper {
    display: none; } }

