.contact-list {
  padding: 0;
  text-align: center;
  list-style: none;
}

.contact-list__item {
  margin-bottom: 23px;
  font-size: 14.5px;
  font-weight: 600;
  letter-spacing: 1.5px;
  color: #4e4d4e;

  &--color {
    font-size: 18.5px;
    letter-spacing: 1.9px;
    color: #8eb574;
  }
}

@media (min-width: $tablet-width) {
  .contact-list__item {
    font-size: 16.5px;
    letter-spacing: 1.7px;
  }
}

@media (min-width: $desktop-width) {
  .contact-list {
    display: flex;
    justify-content: space-between;
    margin: 0 auto;
    width: 1050px;
    margin-bottom: 70px;
  }
}
