.description__color {
  z-index: 1;
}

.description__title {
  margin-top: 158px;
  font-size: 30px;
}

.description {
  display: flex;
  width: 290px;
  margin: 0 auto;
}

.description__block {
  margin-bottom: 40px;
  width: 235px;
  @include font(14px, 15px, 300);
}

.description__button {
  padding-bottom: 5px;
  text-decoration: none;
  text-transform: uppercase;
  color: white;
  @include font(14px, 15px, 600);
  letter-spacing: 1.4px;
  border-bottom: 2px solid white;
}

.description__social {
  display: none;
}

@media (min-width: $tablet-width) {
  .description {
    display: flex;
    width: 290px;
    margin-left: 62px;
  }

  .description__button {
    color: black;
    @include font(16px, 17px, 700);
    letter-spacing: 1.6px;
    border-bottom: 2px solid black;
  }

  .description__block {
    margin-bottom: 43px;
    width: 275px;
    @include font(16px, 17px, 300);
  }

  .description__title {
    margin-top: 280px;
    margin-bottom: 35px;
    font-size: 36px;
  }

  .description__social {
    display: block;
    margin-top: 229px;
    font-size: 12px;
    font-weight: 300;
    letter-spacing: 1.2px;
    color: rgba(33, 32, 33, 0.6);
  }
}

@media (min-width: $desktop-width) {
  .description {
    width: 380px;
    margin-left: 154px;
  }

  .description__title {
    margin-top: 225px;
    margin-bottom: 45px;
    @include font(48px, 48px, 600);
  }

  .description__block {
    margin-bottom: 43px;
    width: 301px;
    @include font(18px, 18px, 300);
  }

  .description__social {
    font-size: 14px;
    letter-spacing: 1.4px;
  }
}
