.photo {
  display: none;
}

@media (min-width: $tablet-width) {
  .photo {
    display: block;
  }

  .photo__phone {
    position: absolute;
    top: 5px;
    left: 525px;
    z-index: 4;
    font-size: 18px;
    font-weight: 600;
    color: #3b3b3b;
  }

  .photo__image img {
    position: absolute;
    top: 163px;
    left: 400px;
    z-index: 4;
  }
}

@media (min-width: $desktop-width) {
  .photo__phone {
    position: absolute;
    top: 16px;
    left: 63%;
    color: #878787;
  }

  .photo__image img {
    left: 46%;
  }
}
