.works {
  width: 100%;
}

.works__container {
  width: 290px;
  margin: 0 auto;
}

.works__header {
  position: relative;
  margin-top: 140px;
  margin-bottom: 50px;
  @include font(17.5px, 20px, 600);
  letter-spacing: 1.75px;
  text-transform: uppercase;
  text-align: center;
  color: #212021;
}

.works__header::before {
  content: "";
  position: absolute;
  top: -60px;
  left: 142px;
  width: 3px;
  height: 31px;
  background-color: #cb98a8;
}

.works__description {
  padding-left: 0;
}

.works__image img {
  margin-bottom: 10px;
}

.works__description-item {
  list-style: none;
}

.works__social {
  margin: 0;
  font-size: 12px;
  font-weight: 300;
  color: #1d1c1c;
}

.works__image-description {
  margin-top: 10px;
  margin-bottom: 68px;
  font-size: 18.5px;
  font-weight: bold;
  color: #1d1c1c;
}

@media (min-width: $tablet-width) {
  .works__container {
    width: 768px;
    margin: 0 auto;
  }

  .works__header {
    margin-top: 262px;
    margin-bottom: 68px;
    font-size: 24px;
    letter-spacing: 2.4px;
  }

  .works__header::before {
    top: -88px;
    left: 381px;
    width: 2px;
    height: 38px;
  }

  .works__description {
    position: relative;
    min-height: 827px;
  }

  .works__description-item {
    position: absolute;

    &--first {
      top: 99px;
      left: 62px;
      width: 240px;
      min-height: 465px;
    }

    &--second {
      top: 0;
      left: 319px;
      width: 184px;
      min-height: 350px;
    }

    &--third {
      top: 351px;
      left: 319px;
      width: 184px;
      min-height: 350px;
    }

    &--fourth {
      top: 115px;
      left: 519px;
      width: 184px;
      min-height: 350px;
    }
  }

  .works__image-description {
    font-size: 15.5px;
  }
}

@media (min-width: $desktop-width) {
  .works__container {
    width: 1440px;
    margin: 0 auto;
  }

  .works__header {
    margin-top: 262px;
    margin-bottom: 106px;
    font-size: 36px;
    font-weight: 600;
    letter-spacing: 3.6px;
  }

  .works__header::before {
    top: -106px;
    left: 719px;
    width: 2px;
    height: 38px;
  }

  .works__description {
    min-height: 1250px;
  }

  .works__description-item {
    position: absolute;

    &--first {
      top: 172px;
      left: 167px;
      width: 414px;
      min-height: 648px;
    }

    &--second {
      top: 0;
      left: 611px;
      width: 317px;
      min-height: 396px;
    }

    &--third {
      top: 573px;
      left: 611px;
      width: 215px;
      min-height: 408px;
    }

    &--fourth {
      top: 200px;
      left: 958px;
      width: 305px;
      min-height: 495px;
    }
  }

  .works__social {
    font-size: 14px;
  }

  .works__image-description {
    font-size: 18px;
  }
}
