.social-list {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  width: 193px;
  margin: 0 auto;
  padding: 17px 0 85px 0;
  list-style: none;
  text-decoration: none;
}

.social-list__item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0;
  padding: -1px 22px 21px 20px;
}

.social-list__item a {
  text-decoration: none;
  color: black;

  &:hover {
    opacity: 0.5;
  }
}

