.page-header {
  width: 100%;
  color: #fffeff;
}

.page-header__background {
  width: 320px;
  margin: 0 auto;
  min-height: 480px;

  background-image: url("../img/daria-litvinova-342840-unsplash-m.jpg");
  background-repeat: no-repeat;
  background-size: 100%;
}

.page-header__color {
  position: absolute;
  top: 0;
  width: 100%;
  margin: 0 auto;
  min-height: 480px;
  opacity: 0.4;
  border-radius: 2px;
  background-color: #000000;
}

.page-header__wrapper {
  display: flex;
}

.page-header__container {
  display: flex;
  justify-content: space-between;
  z-index: 3;
}

.page-header__logo {
  @include font(40px, 20px, 300);
  text-transform: none;
  cursor: pointer;

  &:hover {
    opacity: 0.7;
  }

  &:active {
    opacity: 0.5;
  }
}

@media (min-width: $tablet-width) {
  .page-header {
    color: black;
    background-color: #f8f8f8;
  }

  .page-header__background {
    position: relative;
    width: 768px;
    margin: 0 auto;
    min-height: 825px;
    background-image: none;
  }

  .page-header__color {
    top: 0;
    left: 58%;
    margin: 0 auto;
    min-height: 825px;
    opacity: 1;
    border-radius: 0;
    background-image: url("../img/karly-gomez-360069-unsplash-1.jpg");
    background-color: #cb98a7;
    background-repeat: no-repeat;
  }
}

@media (min-width: $desktop-width) {
  .page-header__background {
    width: 1439px;
    min-height: 780px;
  }

  .page-header__color {
    min-height: 780px;
    background-image: url("../img/karly-gomez-360069-desk.jpg");
  }
}
