@font-face {
  font-weight: 700;
  font-family: "Source Serif Pro";
  font-style: normal;
  font-display: auto;
  src: url("../fonts/Sourceserifprobold.woff2") format("woff2"), url("../fonts/Sourceserifprobold.woff") format("woff");
}

@font-face {
  font-weight: 300;
  font-family: "Source Serif Pro";
  font-style: normal;
  font-display: auto;
  src: url("../fonts/Sourceserifprolight.woff2") format("woff2"), url("../fonts/Sourceserifprolight.woff") format("woff");
}

@font-face {
  font-weight: 600;
  font-family: "Source Serif Pro";
  font-style: normal;
  font-display: auto;
  src: url("../fonts/Sourceserifprosemibold.woff2") format("woff2"), url("../fonts/Sourceserifprosemibold.woff") format("woff");
}

html,
body {
  min-width: 320px;
}

body {
  padding: 0;
  margin: 0 auto;
  width: auto;
  font-family: "Source Serif Pro", "Arial", sans-serif;
  font-size: 12px;
  line-height: 16px;
  color: black;
}

@media (min-width: $tablet-width) {
  body {
    width: auto;
  }
}

@media (min-width: $desktop-width) {
  body {
    width: auto;
  }
}
