.price {
  width: 320px;
  margin: 0 auto;
}

.price__header {
  position: relative;
  margin-top: 140px;
  margin-bottom: 53px;
  font-size: 18px;
  font-weight: 600;
  letter-spacing: 1.8px;
  text-align: center;
  color: #212021;
}

.price__header::before {
  content: "";
  position: absolute;
  top: -60px;
  left: 160px;
  width: 3px;
  height: 31px;
  background-color: #cb98a8;
}

.price__description {
  padding: 0;
  margin: 0;
  margin-left: 15px;
  list-style: none;
}

.price__block {
  width: 220px;
  margin: 0 auto;
  margin-top: 20px;
}

.price__time {
  margin-bottom: 8px;
  font-size: 18.5px;
  font-weight: bold;
  text-align: left;
  color: #8eb574;
}

.price__cost {
  font-size: 18px;
  font-weight: 600;
  text-align: right;
  color: #8eb574;
}

.price__block-text {
  margin-top: 10px;
  margin-bottom: 73px;
}

.price__image-description {
  margin: 0;
  font-size: 14px;
  text-align: center;
  color: #212021;
}

@media (min-width: $tablet-width) {
  .price {
    width: 768px;
    margin: 0 auto;
  }

  .price__header {
    margin-top: 252px;
    margin-bottom: 114px;
    font-size: 24px;
    font-weight: 600;
    letter-spacing: 2.4px;
  }

  .price__header::before {
    top: -100px;
    left: 382px;
    width: 2px;
    height: 38px;
  }

  .price__description {
    position: relative;
    margin: 0;
    min-height: 700px;
  }

  .price__description-item {
    position: absolute;

    &--first {
      top: 102px;
      left: 62px;
    }

    &--second {
      top: 0;
      left: 318px;
    }

    &--third {
      top: 118px;
      left: 521px;
    }
  }

  .price__block--first {
    width: 180px;
  }

  .price__block--second {
    width: 160px;
  }

  .price__block--third {
    width: 150px;
  }

  .price__time,
  .price__cost {
    font-size: 16px;
  }

  .price__block-text--first {
    width: 216px;
  }

  .price__block-text--second {
    width: 171px;
  }

  .price__block-text--third {
    width: 174px;
  }
}

@media (min-width: $desktop-width) {
  .price {
    width: 1440px;
  }

  .price__header {
    margin-top: 262px;
    margin-bottom: 199px;
    font-size: 36px;
    font-weight: 600;
    letter-spacing: 3.6px;
  }

  .price__header::before {
    left: 718px;
  }

  .price__description {
    min-height: 1000px;
  }

  .price__description-item {
    position: absolute;

    &--first {
      top: 172px;
      left: 166px;
    }

    &--second {
      top: 0;
      left: 610px;
    }

    &--third {
      top: 200px;
      left: 957px;
    }
  }

  .price__block--first {
    width: 240px;
  }

  .price__block--second {
    width: 215px;
  }

  .price__block--third {
    width: 190px;
  }

  .price__time {
    margin-bottom: 0;
    font-size: 24px;
    font-weight: bold;
    line-height: 1.25;
  }

  .price__cost {
    font-size: 17.5px;
    font-weight: 600;
    line-height: 1.69;
  }

  .price__block-text--first {
    width: 306px;
    margin-left: 20px;
  }

  .price__block-text--second {
    width: 290px;
  }

  .price__block-text--third {
    width: 260px;
    margin-left: 20px;
  }

  .price__image-description {
    font-size: 18px;
    line-height: 1.33;
  }
}
