.page-footer {
  width: 320px;
  margin: 0 auto;
}

.page-footer__header {
  position: relative;
  margin-top: 132px;
  margin-bottom: 38px;
  font-size: 18px;
  font-weight: 600;
  letter-spacing: 1.8px;
  text-align: center;
  color: #212021;
}

.page-footer__header::before {
  content: "";
  position: absolute;
  top: -60px;
  left: 157px;
  width: 3px;
  height: 31px;
  background-color: #cb98a8;
}

.page-footer__social {
  margin: 0 auto;
}

@media (min-width: $tablet-width) {
  .page-footer {
    width: 768px;
  }

  .page-footer__header {
    margin-top: 300px;
    margin-bottom: 83px;
    font-size: 24px;
    font-weight: 600;
    letter-spacing: 2.4px;
  }

  .page-footer__header::before {
    top: -100px;
    left: 381px;
    width: 2px;
    height: 38px;
  }
}

@media (min-width: $desktop-width) {
  .page-footer {
    width: 1440px;
  }

  .page-footer__header {
    margin-top: 262px;
    margin-bottom: 112px;
    font-size: 36px;
    font-weight: 600;
    letter-spacing: 3.6px;
  }

  .page-footer__header::before {
    left: 719px;
  }
}
