.about-me {
  width: 320px;
  margin: 0 auto;
  text-align: center;
}

.about-me__header {
  position: relative;
  margin-top: 189px;
  margin-bottom: 48px;
  font-size: 18px;
  font-weight: 600;
  letter-spacing: 1.8px;
  text-align: center;
  color: #212021;
}

.about-me__header::before {
  content: "";
  position: absolute;
  top: -60px;
  left: 157px;
  width: 3px;
  height: 31px;
  background-color: #cb98a8;
}

.about-me__wrapper {
  position: relative;
  min-height: 360px;
}

.about-me__image--left {
  position: absolute;
  left: -60px;
}

.about-me__image--right {
  position: absolute;
  top: 35px;
  left: 175px;
}

.about-me__description {
  width: 268px;
  margin-left: 26px;
  font-size: 14px;
  text-align: center;
  color: #1d1c1c;
}

.about-me__description-desktop {
  display: none;
}

.about-me__list {
  padding: 0;
  font-size: 14px;
  color: #1d1c1c;
  list-style: none;
}

.about-me__button {
  padding-bottom: 5px;
  font-size: 14px;
  font-weight: bold;
  letter-spacing: 1.4px;
  color: #212021;
  border-bottom: 2px solid #212021;
}

@media (min-width: $tablet-width) {
  .about-me {
    width: 768px;
    text-align: center;
  }

  .about-me__header {
    margin-top: 352px;
    margin-bottom: 61px;
    font-size: 24px;
    letter-spacing: 2.4px;
  }

  .about-me__header::before {
    top: -100px;
    left: 381px;
    width: 2px;
    height: 38px;
  }

  .about-me__wrapper {
    min-height: 430px;
  }

  .about-me__image--left {
    left: 62px;
  }

  .about-me__image--right {
    top: 48px;
    left: 396px;
  }

  .about-me__description {
    width: 276px;
    margin-left: 76px;
    font-size: 16px;
    text-align: left;
  }

  .about-me__list {
    margin-top: -65px;
    margin-left: 396px;
    font-size: 16px;
    text-align: left;
  }

  .about-me__button {
    margin-left: 228px;
    font-size: 16px;
    letter-spacing: 1.6px;
  }
}

@media (min-width: $desktop-width) {
  .about-me {
    position: relative;
    width: 1440px;
  }

  .about-me__header {
    margin-top: 262px;
    margin-bottom: 106px;
    font-size: 36px;
    font-weight: 600;
    letter-spacing: 3.6px;
  }

  .about-me__header::before {
    left: 727px;
  }

  .about-me__image--left {
    left: 166px;
  }

  .about-me__image--right {
    left: 512px;
  }

  .about-me__description {
    position: absolute;
    top: 250px;
    left: 879px;
    width: 311px;
    margin-left: 0;
    font-size: 18px;
    line-height: 1;
  }

  .about-me__description-desktop {
    display: block;
    position: absolute;
    top: 390px;
    left: 879px;
    width: 290px;
    font-size: 18px;
    text-align: left;
    line-height: 1;
    color: #1d1c1c;
  }

  .about-me__list {
    margin-top: -57px;
    margin-left: 879px;
    margin-bottom: 40px;
    font-size: 18px;
  }

  .about-me__item {
    margin-bottom: 5px;
  }

  .about-me__button {
    margin-left: 522px;
  }
}
